// src/store/modules/stores.js
import { fetchStores as fetchStoresApi } from "@/api/stores";

let currentAbort = null;

export default {
  namespaced: true,
  state: () => ({
    stores: [],
    currentStore: null,
    loading: false,
    error: null,
  }),
  getters: {
    hasStores(state) {
      return state.stores.length > 0;
    },
    storeBySlug: (state) => (slug) => {
      if (!slug) return null;
      const normalized = String(slug).toLowerCase();
      return state.stores.find(
        (s) =>
          s.slug.toLowerCase() === normalized ||
          s.name.toLowerCase() === normalized
      );
    },
  },
  mutations: {
    SET_LOADING(state, value) {
      state.loading = value;
    },
    SET_ERROR(state, error) {
      state.error = error;
    },
    SET_STORES(state, stores) {
      state.stores = stores || [];
    },
    SET_CURRENT_STORE(state, store) {
      state.currentStore = store;
    },
  },
  actions: {
    async fetchStores({ commit, state }) {
      // Si ya hay una petici√≥n en curso, la abortamos
      if (state.loading) {
        if (currentAbort) currentAbort();
      }

      commit("SET_LOADING", true);
      commit("SET_ERROR", null);

      const { promise, abort } = fetchStoresApi();
      currentAbort = abort;

      try {
        const data = await promise;
        commit("SET_STORES", Array.isArray(data) ? data : []);
      } catch (error) {
        if (error.name === "AbortError") return;
        // eslint-disable-next-line no-console
        console.error("Error cargando tiendas:", error);
        commit("SET_ERROR", error.message || "Error cargando tiendas");
      } finally {
        commit("SET_LOADING", false);
        currentAbort = null;
      }
    },

    selectStoreBySlug({ getters, commit }, slug) {
      const store = getters.storeBySlug(slug);
      commit("SET_CURRENT_STORE", store || null);
    },
  },
};
