<!-- src/components/ProductCard.vue -->
<template>
  <v-card
    class="product-card h-100"
    rounded="lg"
    elevation="1"
    @mouseenter="isHovered = true"
    @mouseleave="isHovered = false"
  >
    <!-- Imagen con overlay -->
    <div class="product-image-wrapper position-relative overflow-hidden">
      <v-img
        :src="product.image"
        :alt="product.name"
        height="220"
        class="product-image"
        cover
      />

      <!-- Badge de descuento -->
      <v-chip
        v-if="product.discount"
        class="discount-badge"
        color="error"
        variant="flat"
        size="small"
      >
        -{{ product.discount }}%
      </v-chip>

      <!-- Overlay con acciones r√°pidas -->
      <div v-if="isHovered" class="product-overlay">
        <v-btn
          color="white"
          variant="flat"
          size="small"
          prepend-icon="mdi-heart-outline"
          @click.stop="toggleFavorite"
          class="mb-2"
        >
          Favorito
        </v-btn>
        <v-btn
          color="primary"
          variant="flat"
          prepend-icon="mdi-information-outline"
          @click.stop="showDetails = true"
        >
          Detalles
        </v-btn>
      </div>
    </div>

    <!-- Contenido -->
    <v-card-text class="pa-4">
      <!-- Categor√≠a -->
      <v-chip
        size="x-small"
        variant="tonal"
        color="secondary"
        class="mb-3"
      >
        {{ product.category }}
      </v-chip>

      <!-- Nombre -->
      <h3 class="text-body-1 font-weight-semibold mb-2 line-clamp-2">
        {{ product.name }}
      </h3>

      <!-- Descripci√≥n -->
      <p class="text-caption text-medium-emphasis mb-3 line-clamp-2">
        {{ product.description }}
      </p>

      <!-- Rating -->
      <div class="d-flex align-center mb-3">
        <v-rating
          :model-value="product.rating || 4.5"
          color="warning"
          size="x-small"
          readonly
          density="compact"
        />
        <span class="text-caption text-medium-emphasis ms-2">
          ({{ product.reviews || 0 }})
        </span>
      </div>

      <!-- Stock -->
      <v-progress-linear
        :value="(product.stock / 100) * 100"
        :color="product.stock > 20 ? 'success' : 'warning'"
        height="4"
        class="mb-3"
      />

      <div class="d-flex justify-space-between align-center mb-4">
        <span class="text-caption text-medium-emphasis">
          Stock: {{ product.stock }}
        </span>
      </div>

      <!-- Precio -->
      <div class="price-section mb-4">
        <div v-if="product.oldPrice" class="text-caption text-medium-emphasis text-decoration-line-through">
          ${{ product.oldPrice }}
        </div>
        <div class="text-h5 font-weight-bold text-primary">
          ${{ product.price }}
        </div>
      </div>
    </v-card-text>

    <!-- Acciones -->
    <v-card-actions class="px-4 py-3 d-flex gap-2 actions-container">
      <v-btn
        color="success"
        variant="flat"
        prepend-icon="mdi-whatsapp"
        size="small"
        class="text-none flex-grow-1 btn-whatsapp"
        @click="sendWhatsAppMessage"
      >
        Consultar
      </v-btn>
      <v-tooltip text="Agregar al carrito">
        <template #activator="{ props }">
          <v-btn
            color="primary"
            variant="flat"
            icon="mdi-cart-plus"
            size="large"
            class="btn-cart"
            v-bind="props"
            @click="addToCart"
          />
        </template>
      </v-tooltip>
    </v-card-actions>

    <!-- Dialog de detalles -->
    <ProductDetailDialog
      :show="showDetails"
      :product="product"
      :store="store"
      @close="showDetails = false"
      @add-to-cart="addToCart"
      @send-message="sendWhatsAppMessage"
    />
  </v-card>
</template>

<script>
import ProductDetailDialog from "./ProductDetailDialog.vue";

export default {
  name: "ProductCard",
  components: {
    ProductDetailDialog,
  },
  props: {
    product: {
      type: Object,
      required: true,
    },
    store: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      isHovered: false,
      showDetails: false,
      isFavorite: false,
    };
  },
  methods: {
    addToCart() {
      this.$emit("add-to-cart", this.product);
    },
    toggleFavorite() {
      this.isFavorite = !this.isFavorite;
    },
    sendWhatsAppMessage() {
      const phone = this.store.whatsappPhone;
      const message = encodeURIComponent(
        `Hola, estoy interesado en el producto: *${this.product.name}* ($${this.product.price}). ¬øTienes disponibilidad?`
      );
      window.open(`https://wa.me/${phone}?text=${message}`, "_blank");
    },
  },
};
</script>

