<template>
  <v-dialog
    :model-value="show"
    max-width="600"
    @update:model-value="$emit('close')"
  >
    <v-card rounded="lg">
      <!-- Header -->
      <v-card-title class="d-flex justify-space-between align-center pa-6 bg-light">
        <span class="text-h6 font-weight-bold">
          üõí Mi Carrito
        </span>
        <v-btn
          icon
          variant="text"
          size="small"
          @click="$emit('close')"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>

      <v-divider />

      <!-- Contenido del carrito -->
      <v-card-text class="pa-6">
        <div v-if="items.length === 0" class="text-center py-12">
          <v-icon size="64" color="medium-emphasis" class="mb-4">
            mdi-shopping-cart-outline
          </v-icon>
          <p class="text-body-2 text-medium-emphasis mt-4">
            Tu carrito est√° vac√≠o
          </p>
          <p class="text-caption text-medium-emphasis">
            Agrega productos para comenzar a comprar
          </p>
        </div>

        <div v-else>
          <!-- Items -->
          <v-list class="pa-0 mb-4">
            <v-list-item
              v-for="(item, index) in items"
              :key="index"
              class="mb-3 pa-3 rounded-lg cart-item"
              style="background-color: #f9fafb; border: 1px solid #e5e7eb;"
            >
              <template #prepend>
                <v-img
                  :src="item.image"
                  height="70"
                  width="70"
                  class="rounded me-3"
                />
              </template>

              <div class="flex-grow-1">
                <v-list-item-title class="font-weight-semibold mb-1">
                  {{ item.name }}
                </v-list-item-title>
                <v-list-item-subtitle class="text-caption">
                  {{ item.category }}
                </v-list-item-subtitle>
              </div>

              <template #append>
                <div class="text-right">
                  <div class="text-body-2 font-weight-bold text-primary mb-2">
                    ${{ item.price }}
                  </div>
                  <v-btn
                    size="x-small"
                    color="error"
                    variant="text"
                    icon
                    @click.stop="removeItem(index)"
                  >
                    <v-icon>mdi-trash-outline</v-icon>
                  </v-btn>
                </div>
              </template>
            </v-list-item>
          </v-list>

          <!-- Resumen -->
          <v-divider class="my-4" />

          <div class="summary-section">
            <div class="d-flex justify-space-between mb-3">
              <span class="text-body-2">Subtotal:</span>
              <span class="text-body-2 font-weight-bold">${{ subtotal }}</span>
            </div>
            <div class="d-flex justify-space-between mb-3">
              <span class="text-body-2">Impuesto (10%):</span>
              <span class="text-body-2 font-weight-bold">${{ tax }}</span>
            </div>
            <v-divider class="my-3" />
            <div class="d-flex justify-space-between">
              <span class="text-h6 font-weight-bold">Total:</span>
              <span class="text-h6 font-weight-bold text-primary">${{ total }}</span>
            </div>
          </div>
        </div>
      </v-card-text>

      <!-- Acciones -->
      <v-card-actions v-if="items.length > 0" class="pa-6 d-flex gap-3 actions-cart-dialog">
        <v-btn
          variant="outlined"
          color="primary"
          size="large"
          class="btn-continue"
          @click="$emit('close')"
        >
          Continuar comprando
        </v-btn>
        <v-btn
          color="success"
          variant="flat"
          prepend-icon="mdi-whatsapp"
          class="flex-grow-1 btn-send-order"
          size="large"
          @click="sendMessage"
        >
          Enviar Pedido
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "CartDialog",
  props: {
    show: Boolean,
    store: Object,
  },
  computed: {
    ...mapState("cart", ["items"]),
    subtotal() {
      return this.items.reduce((sum, item) => sum + item.price, 0).toFixed(2);
    },
    tax() {
      return (this.subtotal * 0.1).toFixed(2);
    },
    total() {
      return (parseFloat(this.subtotal) + parseFloat(this.tax)).toFixed(2);
    },
  },
  methods: {
    removeItem(index) {
      this.$store.commit("cart/REMOVE_ITEM", index);
    },
    sendMessage() {
      if (!this.store) return;
      
      const phone = this.store.whatsappPhone;
      const itemsList = this.items
        .map((item) => `‚Ä¢ ${item.name} - $${item.price}`)
        .join("\n");

      const message = encodeURIComponent(
        `Hola, me gustar√≠a realizar el siguiente pedido:\n\n${itemsList}\n\n‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ\nSubtotal: $${this.subtotal}\nImpuesto: $${this.tax}\n*Total: $${this.total}*\n‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ‚îÅ\n\nPor favor, confirma disponibilidad y formas de pago. Gracias.`
      );

      window.open(`https://wa.me/${phone}?text=${message}`, "_blank");
      this.$emit("send-message");
      this.$emit("close");
    },
  },
};
</script>

