                  <h1 class="text-h4 font-weight-bold mb-2">
                    üõçÔ∏è Multi tienda moderna
                  </h1>
                  <p class="text-body-2 text-medium-emphasis mb-4">
                    Elige una tienda para explorar productos curados por categor√≠a.
                  </p>

                  <div class="d-flex flex-wrap align-center ga-2 text-caption text-medium-emphasis">
                    <span>Tambi√©n puedes usar la URL:</span>
                    <v-chip size="small" variant="outlined" color="primary">
                      ?store=tech
                    </v-chip>
                    <v-chip size="small" variant="outlined" color="primary">
                      ?store=fashion
                    </v-chip>
                  </div>
                </v-col>

                <v-col
                  cols="12"
                  md="4"
                  class="d-flex justify-end align-center"
                >
                  <v-card
                    class="text-center pa-4"
                    elevation="0"
                    color="primary"
                    variant="tonal"
                  >
                    <div class="text-caption text-medium-emphasis mb-1">
                      Tiendas disponibles
                    </div>
                    <div class="text-h3 font-weight-bold text-primary">
                      {{ stores.length }}
                    </div>
                  </v-card>
