              </v-row>
            </v-sheet>

            <!-- Grid de tiendas con animaci√≥n -->
            <v-row dense class="mb-6">
              <v-col
                v-for="(store, index) in stores"
                :key="store.slug"
                cols="12"
                sm="6"
