                      {{ store.products.length }} productos
                    </span>
                    <v-btn
                      variant="flat"
                      color="primary"
                      size="small"
                      class="text-none btn-open-store"
                      append-icon="mdi-arrow-right"
                    >
                      Abrir
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-col>
            </v-row>
          </div>

          <!-- Con ?store: vista de tienda -->
          <div v-else>
            <v-btn
              variant="text"
              color="primary"
              prepend-icon="mdi-arrow-left"
              class="mb-4 text-none px-0"
