                        :show-description="true"
                      />
                    </v-col>
                    <v-col cols="12" md="4" class="d-flex justify-end">
                      <v-chip
                        size="large"
                        color="success"
                        variant="tonal"
                        prepend-icon="mdi-shopping-cart"
                      >
                        {{ cartItems }} en carrito
                      </v-chip>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-sheet>

              <!-- Productos Header -->
              <div class="d-flex align-center justify-space-between mb-4 products-header">
                <div>
                  <h2 class="text-h5 font-weight-medium mb-0">
                    üì¶ Cat√°logo de Productos
                  </h2>
                </div>

                <div class="d-flex gap-2">
                  <v-chip
                    size="small"
                    color="primary"
                    variant="tonal"
                  >
                    {{ currentStore.products.length }} art√≠culo(s)
                  </v-chip>
                  <v-btn
                    v-if="cartItems > 0"
                    color="success"
                    variant="flat"
                    size="small"
                    prepend-icon="mdi-whatsapp"
                    class="btn-view-cart"
                    @click="showCartDialog = true"
                  >
                    Ver Carrito
                  </v-btn>
                </div>
              </div>

              <v-divider class="mb-6" />

              <!-- Grid de productos -->
              <v-row dense>
                <v-col
                  v-for="product in currentStore.products"
                  :key="product.id"
                  cols="12"
                  sm="6"
                  md="4"
                  lg="3"
                >
                  <ProductCard
                    :product="product"
                    :store="currentStore"
                    @add-to-cart="addToCart"
                  />
                </v-col>
              </v-row>
            </div>

            <v-alert
              v-else
              type="warning"
              variant="tonal"
              border="start"
              density="comfortable"
              class="mt-6"
            >
              No se encontr√≥ la tienda
              <strong class="mx-1">{{ storeParam }}</strong>.
              Elige una tienda v√°lida.
            </v-alert>
          </div>
        </template>
      </v-col>
    </v-row>

    <!-- Dialog del carrito -->
    <CartDialog
      :show="showCartDialog"
      :store="currentStore"
      @close="showCartDialog = false"
      @send-message="sendCartMessage"
    />
  </v-container>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
import StoreHeader from "@/components/StoreHeader.vue";
import ProductCard from "@/components/ProductCard.vue";
import CartDialog from "@/components/CartDialog.vue";

export default {
  name: "StoreView",
  components: {
    StoreHeader,
    ProductCard,
    CartDialog,
  },
  data() {
    return {
      showCartDialog: false,
    };
  },
  computed: {
    ...mapState("stores", ["stores", "currentStore", "loading", "error"]),
    ...mapState("cart", ["items"]),
    ...mapGetters("stores", ["hasStores"]),
    storeParam() {
      return this.$route.query.store || null;
    },
    cartItems() {
      return this.items.length;
    },
  },
  watch: {
    storeParam: {
      immediate: true,
      handler(newValue) {
        if (!this.hasStores) return;
        if (newValue) {
          this.selectStoreBySlug(newValue);
          this.updateTitle();
        } else {
          this.$store.commit("stores/SET_CURRENT_STORE", null);
          document.title = "Multi Tienda Vue";
        }
      },
    },
    currentStore(store) {
      if (store) {
        this.updateTitle();
      }
    },
  },
  created() {
    if (!this.hasStores) {
      this.fetchStores().then(() => {
        if (this.storeParam) {
          this.selectStoreBySlug(this.storeParam);
          this.updateTitle();
        }
      });
    } else if (this.storeParam) {
      this.selectStoreBySlug(this.storeParam);
      this.updateTitle();
    }
  },
  methods: {
    ...mapActions("stores", ["fetchStores", "selectStoreBySlug"]),
    ...mapActions("cart", ["addItem"]),
    goToStore(slug) {
      this.$router.push({ name: "Store", query: { store: slug } });
    },
    clearStore() {
      this.$router.push({ name: "Store", query: {} });
    },
    updateTitle() {
      if (!this.currentStore) return;
      const base = "Multi Tienda Vue";
      const title = `${this.currentStore.name} - ${base}`;
      document.title = title;
    },
    addToCart(product) {
      this.addItem(product);
    },
    sendCartMessage() {
      // Enviado desde CartDialog
    },
  },
};
</script>

