<!-- src/components/StoreHeader.vue -->
<template>
  <header class="store-header" :style="headerStyle">
    <div class="store-logo" v-if="store.logoBase64">
      <img :src="store.logoBase64" :alt="store.name" />
    </div>
    <div class="store-info">
      <h2 class="store-name">{{ store.name }}</h2>
      <p v-if="showDescription && store.description" class="store-description">
        {{ store.description }}
      </p>
      <p v-else-if="!showDescription && store.description" class="store-description truncated">
        {{ store.description }}
      </p>
    </div>
  </header>
</template>

<script>
export default {
  name: 'StoreHeader',
  props: {
    store: {
      type: Object,
      required: true
    },
    showDescription: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    headerStyle() {
      return {
        borderBottom: `3px solid ${this.store.primaryColor || '#42b883'}`
      }
    }
  }
}
</script>

<style scoped>
.store-header {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.store-logo {
  position: relative;
}

.store-logo img {
  width: 72px;
  height: 72px;
  object-fit: cover;
  border-radius: 20px;
  border: 3px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.12);
  transition: all 0.3s ease;
  display: block;
}

.store-logo img:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 32px rgba(15, 23, 42, 0.15);
}

.store-info {
  flex: 1;
}

.store-name {
  margin: 0;
  font-size: 1.75rem;
  font-weight: 700;
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.01em;
}

.store-description {
  margin: 0.5rem 0 0;
  font-size: 0.95rem;
  color: #64748b;
  font-weight: 500;
  letter-spacing: 0.01em;
}

.truncated {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>
