            </span>
          </div>
          <v-progress-linear
            :value="(product.stock / 100) * 100"
            :color="product.stock > 20 ? 'success' : 'warning'"
            height="6"
          />
        </div>

        <!-- Precios -->
        <div class="price-box mb-6">
          <div v-if="product.oldPrice" class="old-price">
            Antes: <span class="text-decoration-line-through">${{ product.oldPrice }}</span>
          </div>
          <div class="current-price">
            Ahora: <span class="text-h4 font-weight-bold text-primary">${{ product.price }}</span>
          </div>
        </div>

        <v-divider class="mb-4" />

        <!-- Informaci√≥n adicional -->
        <div class="info-grid mb-4">
          <div class="info-item">
            <v-icon color="primary" size="small">mdi-truck-fast</v-icon>
            <span class="text-caption ms-2">Env√≠o r√°pido disponible</span>
          </div>
          <div class="info-item">
            <v-icon color="primary" size="small">mdi-shield-check</v-icon>
            <span class="text-caption ms-2">100% garantizado</span>
          </div>
        </div>
      </v-card-text>

      <!-- Acciones -->
      <v-card-actions class="pa-6 d-flex gap-3 actions-dialog">
        <v-btn
          color="success"
          variant="flat"
          prepend-icon="mdi-whatsapp"
          class="flex-grow-1 btn-whatsapp-dialog"
          size="large"
          @click="handleSendMessage"
        >
          Consultar por WhatsApp
        </v-btn>
        <v-btn
          color="primary"
          variant="flat"
          prepend-icon="mdi-cart-plus"
          size="large"
          class="flex-grow-1 btn-cart-dialog"
          @click="handleAddToCart"
        >
          Agregar al carrito
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "ProductDetailDialog",
  props: {
    show: Boolean,
    product: Object,
    store: Object,
  },
  methods: {
    handleAddToCart() {
      this.$emit("add-to-cart", this.product);
      this.$emit("close");
    },
    handleSendMessage() {
      this.$emit("send-message");
      this.$emit("close");
    },
  },
};
</script>

