// src/api/stores.js
import { createAbortableFetch } from "./httpClient";

/**
 * Devuelve la lista de tiendas desde el JSON est√°tico.
 */
export function fetchStores() {
  // /stores.json viene de la carpeta public
  return createAbortableFetch(`${process.env.BASE_URL}stores.json`, {
    method: "GET",
  });
}
